// src/components/SignupStep1.js
import React from 'react';
import { useNavigate } from 'react-router-dom'; 
import './SignupStep1.css';

const SignupStep1 = () => {
  const navigate = useNavigate();

  const handleNextStep = () => {
    navigate('/signup-step2'); //  '/signup-step2' 
  };

  return (
    <div className="done-container">
      <h2>
         <br />
          
      </h2>
      <p className='description-text'>   .<br />
      ex) 
      </p>
      
      {/* Name field */}
      <input type="text" placeholder="" className="input-field" />
      
      <button className="done-button" onClick={handleNextStep}></button> {/* onClick   */}
    </div>
  );
};

export default SignupStep1;
